.App {
  text-align: center;
}

.App-logo {
  height: 50vmin;
  pointer-events: none;
}

.App-header {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
}

.App-link {
  color: #5dc5be;
  animation: colorTransition 2s infinite alternate;
} 

@keyframes colorTransition {
  0% {
    color: #5dc5be;
  }
  100% {
    color: #fff;
  }
} 
